import Vue from 'vue'
import Router from 'vue-router'
import { Dialog } from 'vant';

Vue.use(Router)

//捕获重定向错误
const origin = Router.prototype.push;
Router.prototype.push = function push(localtion){
 return origin.call(this,localtion).catch(err=>err)
}

const router= new Router({
  routes: [
    {
      path: '*',
      component:()=>import("../page/layout.vue"),
    },
    {
      path: '/',
      redirect:"index",
      component:()=>import("../page/layout.vue"),
      children:[
        {
          path: 'index',
          component:()=>import("../page/index.vue"),
        },
        {
          path: 'cate',
          component:()=>import("../page/cate.vue"),
        },
        {
          path: 'shopp',
          component:()=>import("../page/shopp.vue"),
          beforeEnter(to,from,next){
            var user=sessionStorage.getItem("user")
            if(user){
              next()
            }else{
              Dialog.alert({
                message: '请先登录！',
                theme: 'round-button',
              }).then(() => {
                // on close
              });
              next('/login')
            }
          }
        },
        {
          path: 'my',
          component:()=>import("../page/my.vue"),
          beforeEnter(to,from,next){
            var user=sessionStorage.getItem("user")
            if(user){
              next()
            }else{
              Dialog.alert({
                message: '请先登录！',
                theme: 'round-button',
              }).then(() => {
                // on close
              });
              next('/login')
            }
          }
        },
        {
          path: 'goodslist',
          component:()=>import("../page/goodslist.vue"),
        },
        {
          path: 'login',
          component:()=>import("../page/login.vue"),
        },
        {
          path: 'register',
          component:()=>import("../page/register.vue"),
        }
      ]
    }
  ]
})

//设置全局前置守卫
router.beforeEach((to,from,next)=>{
  var user=sessionStorage.getItem("user")
  if(user){
    if(to.path=="/login"){
      next('/')
    }
  }
  next()
})
export default router


